.all-items{
    background-color: white;
    width: 30%;
    height: fit-content;
    border-radius: 10px;
}
   
.all-items-dark{
    background-color:gainsboro;
    width: 30%;
    height: fit-content;
    border-radius: 10px;
}
#cancel{
    color: black;
    font-weight: 500;
    font-size: large;
    border: none;
    outline: none;
    background: 0;
    padding: 0 0.4rem;
    cursor: pointer;
    float: right;
    margin-top: -0.3%;
}
#pcancel{
    color: black;
    font-weight: 500;
    font-size: large;
    position: absolute;
    right: 37.2rem;
    top: 6.1rem;
    border: none;
    outline: none;
    background: 0;
    padding: 0 0.4rem;
    cursor: pointer;
}
#pcancel:hover{
    background-color: red;
    padding:0 0.4rem ;
    color: white;
    border-radius: 3px;
}
#cancel:hover{
    background-color: red;
    padding:0 0.4rem ;
    color: white;
    border-radius: 3px;
}

/* .search-items-dark{
    display: flex;
    align-items: center;
    flex-direction: column;
} */
.send-button{
    display: flex;
    justify-content: center;
    align-items:  center;
    padding-bottom: 1rem;
}
.searchcreate{
    /* padding: 0.4rem 3rem; */
    outline: none;
    border: none;
    background-color: cyan;
    border-radius: 6px;
    color: white;
    cursor: pointer;
    letter-spacing: 1px;
    font-size: large;
    margin-left: 0;
    padding: 0.7rem 1.3rem;
}
.searchcreate:hover{
    opacity: 0.5;
}

.search-items input{
    margin-top: 2rem;
    margin-left: 2rem;
    outline: none;
    background: 0;
    border: none; 
    width: 80%;
    border-bottom: 1px solid black;
}
.search-items input:focus{
    outline: none;
    background: 0;
    border: none;
    border-bottom: 1px solid black;
}
.search-items input:focus::placeholder{
    color: cyan;
    position: absolute;
    bottom: 10px;
}
 
@media (max-width:1120px){
    .all-items{
        background-color: white;
        width: 50%;
        height: fit-content;
        border-radius: 10px;
    }
       
    .all-items-dark{
        background-color:gainsboro;
        width: 50%;
        height: fit-content;
        border-radius: 10px;
    }
}
@media (max-width:810px){
    .all-items{
        background-color: white;
        width: 65%;
        height: fit-content;
        border-radius: 10px;
    }
       
    .all-items-dark{
        background-color:gainsboro;
        width: 65%;
        height: fit-content;
        border-radius: 10px;
    }
}
